import {  
    mapMutations  
} from 'vuex';
import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			couponList: [],
			tabCurrentIndex: 0,
			currentpage: 1,
			totalPages: 1,
			istabclick: true,
			navList: [{
					state: 0,
					text: '未使用'
				},
				{
					state: 1,
					text: '已使用'
				},
				{
					state: 2,
					text: '已过期'
				}
			],
			state: 0
		};
	},
	onLoad(option){
		this.mycoloect()
	},
	onPullDownRefresh() {
		this.currentpage = 1
		this.mycoloect(true)
	},
	onReachBottom() {
		if(this.currentpage >= this.totalPages) return false
		this.currentpage++;
		this.mycoloect()
	},
	methods:{
		mycoloect(flag) {
			uni.showLoading({title: '正在加载'})
			let url = '/coupon/mylist?status='+this.state+'&page='+this.currentpage+'&limit=10'
			api.httpget(url).then((res) => {
				let respons = res.data;
				if(respons.errno == 0) {
					if(flag || this.istabclick) this.couponList = []
					this.couponList = this.couponList.concat(respons.data.list)
					this.totalPages = respons.data.pages
					this.istabclick = false
				}
				if(flag) uni.stopPullDownRefresh();
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
				if(flag) uni.stopPullDownRefresh();
			})
		},
		//顶部tab点击
		tabClick(index,state){
			this.tabCurrentIndex = index;
			this.state = state
			this.istabclick = true
			this.currentpage = 1;
			this.mycoloect()
		},
		//详情
		navToDetailPage(item){
			uni.navigateTo({
				url: `/pages/product/product?goodsId=${id}`
			})
		}
	}
}
